/*
/*/

.only-(sp/tab/pc).(sp/tab/pc)

.hidden-(sp/tab/pc).(sp/tab/pc)

.only-sptab/pc.only-pctab/sp

Markup:

Styleguide #{$guideNum}
============================================================*/
.hidden {
	display: none !important;
}
.UA-pc {
	.only-uaSp {
		display: none !important;
	}
}
.UA-sp {
	.only-uaPc {
		display: none !important;
	}
}
.only-print {
	display: none !important;
}
@media screen and (max-width: $breakpoint-sp) {
	.hidden-sp,
	.only-pc,
	.only-tab,
	.only-print {
		display: none !important;
	}
}

@media screen and (min-width: $breakpoint-sp + 1) {
	.hidden-pc,
	.only-tab,
	.only-sp,
	.only-print {
		display: none !important;
	}
}

@media print {
	.hidden-print {
		display: none !important;
	}
}